
.preloader {
	width: 100%;
	margin-top: 8px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;

	.dot {
		width: 10px;
		height: 10px;
		border-radius: 100%;
		background-color: #4d8eda;
		position: absolute;
		left: 30%;
		animation: easeInAndOut 2.5s ease infinite;

		&:nth-child(1) {
			animation-delay: 0s;
			opacity: 0;
		}
		&:nth-child(2) {
			animation-delay: 0.3s;
			opacity: 0.8;
		}
		&:nth-child(3) {
			animation-delay: 0.6s;
			opacity: 0.5;
		}
		&:nth-child(4) {
			animation-delay: 0.9s;
			opacity: 0.2;
		}
	}
}

@keyframes easeInAndOut {
	0% {
		left: 10%;
		opacity: 0;
	}
	48% {
		left: 49%;
		opacity: 1;
	}
	50% {
		left: 50%;
		opacity: 1;
	}
	70% {
		left: 70%;
		opacity: 0;
	}
}
